@import url(https://fonts.googleapis.com/css?family=Oswald|Libre+Baskerville|Ranga);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  /* display: flex; */
  /* background-image: url("../Images/5022.png") */
}

header {
  display: flex;
  width: 100%;
  height: 80px;
}

.navbarMain {
  margin-top: 10px;
}

.App-div {
  background-color: #01153b;
  margin-top: -20px;
  min-height: 88vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}
.aboutMain{
width: 90%;
height: 600px;

/* background-color: red; */
/* position: fixed; */
}
.aboutWrapper {
    margin-left: auto;
    margin-right: auto;
    margin-top: 100px;
    /* background-color: blue; */
    width: 1000px;
    height: 500px;
}
#headshot {
    border: #c99e76 solid 3px;
    border-style: ridge;
    border-radius: 50%;
    width: 300px;
    margin-top: 30px;
    position: absolute;
}
.textWrapper {
    width: 600px;
    margin-left: 400px;
    font-family: 'Libre Baskerville', serif;
    position: absolute;
}
.logo {
    height: 10vmin;
    margin-top: 5px
  }
#navBarMain {
    display: inline-flex;
    margin-left: auto;
    margin-right: auto;
    width: 600px;
    justify-content: space-around;
    align-items: center;
}

.navLinks {
    font-size: 25px;
    text-decoration: none;  
    color: black;
    font-family: 'Ranga', cursive;
  }

.landingHead {
    font-family: 'Poppins', sans-serif;
}
.body {
    text-align: center;
    height: 500px;
    background-image: url(/porfolio/static/media/woodgrain.a279a640.jpg);
    background-size: 100%;
    border: #c99e76 solid 3px;
    border-style: ridge;
    font-family: 'Libre Baskerville', serif;
    width: 100%;

}
.landingText {
    margin-top: 100px;
}

.description {
    margin-top: 20px;
}
.projectMain{
    width: 1300px;
    height: 530px;
    position: fixed;
    font-family: 'Libre Baskerville', serif;
    display: flex;
    }
.projectContainer {
    background-color: purple;
    position: relative;
    background-size: 100%;
    width: 1050px;
    height: 510px;
}
.mainItem{
    width: 706px;
    height: 530px;
    text-align: center;
    margin-left: 60px;
    margin-right: auto;
    }
.itemImage{
    background-size: 100%;
    width: 700px;
    height: 400px;
    border: #c99e76 solid 3px;
    background-size: 100%;
    border-style: ridge;
    display: block;
}
.projectH3 {
    margin-left: 20px;
}

.sidebar {
    flex-direction: column;
    height: 500px;
}
.sideName {
    font-size: 15px;
}
li {
    list-style-type: none;
}
.sideName:focus {
    color: #c99e76;
}
.projectButton:active {
    color: #c99e76;
}
.projectText{
    transition: all .5s ease-in-out;
    margin-top: 50px;
    font-size: 20px;
}

button {
    background:none!important;
    color:inherit;
    border:none; 
    padding:0!important;
    font: inherit;
}


.grow { transition: all .4s ease-in-out; }
.grow:hover { -webkit-transform: scale(1.1); transform: scale(1.1); }
